import Axios, { AxiosRequestConfig, AxiosResponse } from 'axios'
import Vue from 'vue'

const instance = Axios.create({ baseURL: 'http://192.168.100.155:3000', method: 'post' })

export const req = (config: AxiosRequestConfig = {}): Promise<AxiosResponse> => {
    return new Promise((resolve, reject) => {
        instance.request<any>(config)
            .then((response: AxiosResponse) => {
                resolve(response);
            })
            .catch((err: any) => reject(err));
    });


};

export const init = () => {

    Vue.prototype.$server = {
        req,
        instance
    }

}