
























































































































































































import { Component, Vue } from "vue-property-decorator";
import HelloWorld from "./components/HelloWorld.vue";
import { QrcodeStream } from "vue-qrcode-reader";
import moment from "moment";

@Component({
  components: {
    HelloWorld,
    QrcodeStream,
  },
})
export default class App extends Vue {
  private side = true;
  private letsGo = true;
  private error = "";
  private errors = {
    employeeNotFound: "Aucun employé avec cette carte n'est trouvé. Réessayez.",
    notSignIn: "Vous n'avez pas pointé à l'entrée.",
    allreadySignedOut: "Vous avez déjà émargé à la sortie.",
    allreadySignedIn: "Vous avez déjà émargé à l'entrée.",
    internalError: "Une error innatendu s'est produite. Réessayez.",
    badRequest: "Les données transmises aux serveur ne sont pas correctes.",
  };
  private sign = false;
  private loading = false;
  private timing = 0;
  private formatDate(date: string, format = "HH:mm") {
    return moment(date).format(format);
  }

  private go() {
    this.letsGo = true;
    this.error = "";
    this.sign = false;
    this.loading = true;
  }

  private async onInit(promise: any) {
    this.loading = true;

    try {
      await promise;
    } finally {
      this.loading = false;
    }
  }

  private onDecode(matricule: string) {
    this.letsGo = false;
    this.error = "";
    this.sign = false;
    this.loading = true;

    this.$server
      .req({
        url: "/point/sign",
        data: { matricule: String(matricule), type: this.side ? "IN" : "OUT" },
      })
      .then((res) => {
        this.sign = res.data;
        setTimeout(() => {
          if (!this.letsGo) {
            this.go();
          }
        }, 5000);
      })
      .catch((err) => {
        this.error = err.response.data;
        setTimeout(() => {
          if (!this.letsGo) {
            this.go();
          }
        }, 5000);
      })
      .finally(() => {
        this.loading = false;
      });
  }
}
